import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
    routes: [{
            path: '/PCpage',
            name: 'PCpage',
            component: () =>
                import ('../views/PCpage.vue')
        },
        {
            path: '/',
            name: 'mobile',
            component: () =>
                import ('../views/Mobilepage.vue')
        }
    ]
})


