import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
    // 存储的共享数据
    state:{
        auto_orient:false,
        placeList:[
            {value:'学校大门'},
            {value:'计算机学院'},
            {value:'教学服务中心'},
            {value:'地信学院'},
            {value:'教职工食堂西门'},
            {value:'教职工食堂东门'},
            {value:'生环国重'},
            {value:'公教一'},
            {value:'公教二'},
            {value:'地矿国重'},
            {value:'环境学院'},
            {value:'校医院'},
            {value:'学二食堂'},
            {value:'学一食堂东门'},
            {value:'学一食堂西门'},
            {value:'学一食堂北门'},
            {value:'主席台'},
            {value:'图书馆'},
            {value:'教二南门'},
            {value:'教一北门'},
            {value:'喷泉广场'},
            {value:'北足球场1'},
            {value:'北足球场2'},
            {value:'北篮球场'},
            {value:'材化学院'},
            {value:'网球场'},
            {value:'南足球场'},
            {value:'南篮球场'},
            {value:'南排球场'},
            {value:'二组团一栋'},
            {value:'二组团二栋'},
            {value:'二组团五栋'},
            {value:'二组团四栋'},
            {value:'二组团三栋'},
            {value:'一组团一栋'},
            {value:'一组团二栋'},
            {value:'一组团五栋'},
            {value:'一组团三栋'},
            {value:'一组团四栋'},
            {value:'留学生一栋'},
            {value:'留学生二栋'},
            {value:'三组团一栋'},
            {value:'三组团二栋'},
            {value:'三组团三栋'},
            {value:'三组团四栋'},
            {value:'三组团五栋'},
            {value:'三组团六栋'},
            {value:'博士生一栋'},
            {value:'博士生二栋'},
            {value:'博士生三栋'},
            {value:'观景台'},
            {value:'大学生活动中心'},
            {value:'经管学院'},
            {value:'公园'}
        ]
    },

    // 供外部调用的方法集合
    actions:{
        changeLocationState(ctx,value){
            ctx.commit('changeLocationState',value)
        },
        changeCorse(ctx,data){
            ctx.commit('changeCorse',data);
        }
    },

    // 实际执行操作的方法集合
    mutations:{
        changeLocationState(state,value){
            state.auto_orient = value;
        },
        changeCorse(state,data){
            state.Corse = data;
        }
    }
})


